@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700;900&display=swap');

*,:after,:before{box-sizing:border-box}
.clearfix:after,.clearfix:before{content:'';display:table}
.clearfix:after{clear:both;display:block}
a{color:inherit;text-decoration:none}

.home {
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    font-size: large;
    font-family: 'Montserrat', sans-serif;
    font-weight: 900;
}

.pic {
    width: 50px;
    height: 50px;
    background-image: url("../pictures/BackLogo.png");
    background-size: cover;
    margin-left: 1em;
    cursor: pointer;
}

.login_wrap{
    width:50vmin;
    height:55em;
    min-width: 400px;
    min-height: 600px;
    position:relative;
    background:url("../pictures/landscape-3549862_1920 (1).jpg");
    background-size: cover;
    box-shadow:0 12px 15px 0 rgba(0,0,0,.24),0 17px 50px 0 rgba(0,0,0,.19);
    margin: 10% auto;
}
.login_html{
    width:100%;
    height:100%;
    position:absolute;
    padding:7vmin 7vmin 5vmin 7vmin;
    background: rgba(0, 0, 0, 0.45);
}
.login_html .sign_in_htm,
.login_html .sign_up_htm{
    top:0;
    left:0;
    right:0;
    bottom:0;
    position:absolute;
    transform:rotateY(180deg);
    backface-visibility:hidden;
    transition:all .4s linear;
}
.login_html .sign_in,
.login_html .sign_up,
.login_form .group .check{
    display:none;
}
.login_html .tab,
.login_form .group .label,
.login_form .group .button{
    text-transform:uppercase;
    cursor: pointer;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
}
.login_html .tab{
    font-size:22px;
    margin-right:15px;
    padding-bottom:5px;
    margin:0 15px 10px 0;
    display:inline-block;
    border-bottom:2px solid transparent;
    color: rgba(250, 245, 245, 0.5);
    cursor: pointer;
}
.login_html .sign_in:checked + .tab,
.login_html .sign_up:checked + .tab{
    color:#fff;
    border-color: #fafafa;
}
.login_form{
    min-height:345px;
    position:relative;
    perspective:1000px;
    transform-style:preserve-3d;
}
.login_form .group{
    margin-bottom:15px;
}
.login_form .group .label,
.login_form .group .input,
.login_form .group .button{
    width:100%;
    color:#fff;
    display:block;
}
.login_form .group .input,
.login_form .group .button{
    border:none;
    padding:15px 20px;
    border-radius:25px;
    background: rgba(255, 255, 255, 0.43);
}
.login_form .group input[data-type="password"]{
    text-security: circle;
    -webkit-text-security:circle;
}
.login_form .group .label{
    color:#aaa;
    font-size:12px;
}
.login_form .group .button{
    background: #000000;
    border-color: white;
    border-width: thin;
    border-style: solid;
}
.login_form .group label .icon{
    width:15px;
    height:15px;
    border-radius:2px;
    position:relative;
    display:inline-block;
    background:rgba(255,255,255,.1);
}
.login_form .group label .icon:before,
.login_form .group label .icon:after{
    content:'';
    width:10px;
    height:2px;
    background:#fff;
    position:absolute;
    transition:all .2s ease-in-out 0s;
}
.login_form .group label .icon:before{
    left:3px;
    width:5px;
    bottom:6px;
    transform:scale(0) rotate(0);
}
.login_form .group label .icon:after{
    top:6px;
    right:0;
    transform:scale(0) rotate(0);
}
.login_form .group .check:checked + label{
    color:#fff;
}
.login_form .group .check:checked + label .icon{
    background: #eeebeb;
}
.login_form .group .check:checked + label .icon:before{
    transform:scale(1) rotate(45deg);
}
.login_form .group .check:checked + label .icon:after{
    transform:scale(1) rotate(-45deg);
}
.login_html .sign_in:checked + .tab + .sign_up + .tab + .login_form .sign_in_htm{
    transform:rotate(0);
}
.login_html .sign_up:checked + .tab + .login_form .sign_up_htm{
    transform:rotate(0);
}

input:focus {
    outline: none;
}

.hr{
    height:2px;
    margin:30px 0 30px 0;
    background:rgba(255,255,255,.2);
}
.foot_lnk{
    text-align:center;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 3em;
}

.group {
    margin-top: 2em;
}

a {
    cursor: pointer;
}

.input .button {
    height: 8vmin;
    max-height: 40px;
}